// import { Button } from '@chakra-ui/react'
import { Flex, Heading } from '@chakra-ui/react'
import { useTranslation } from 'react-i18next'
import { useNavigate } from 'react-router-dom'
import Button from '../components/UI/button/Button'
import LoadingSpinner from '../components/UI/loadingSpinner/LoadingSpinner'
import CustomTable from '../components/tables/customTable/CustomTable'
import { customersHeader } from '../constants/tables'
import useDataFetch from '../hook/useDataFetch'
import { IProposalTable } from '../interfaces/Itable'

const Proposal = () => {
  const {t}=useTranslation()
  const navigate=useNavigate();
  const PortalProposalUrl=`${process.env.REACT_APP_API_URL}/api/portal/proposals`;
  const { data, error, loading } = useDataFetch<IProposalTable[]>(PortalProposalUrl);
  let proposalTable;
  if (loading) {
    proposalTable = <LoadingSpinner />;
  }
  if (error) {
    proposalTable = (
        <CustomTable headData={customersHeader} bodyData={[]} limit={10} />)
  }
  if (!loading && data) {
    proposalTable = (
  <CustomTable headData={customersHeader} bodyData={data} limit={10} />
);
}
  return (
    <>
       <section>
        <Flex mb={4} alignItems={'center'} justify={'space-between'}>
        <Heading color='red'>{t("proposals")}</Heading>
       <Button outline="true" onClick={()=>navigate("/proposals/create")}>
          {t("createProposal")}
        </Button></Flex>
        {proposalTable}
  </section>
  </>
  )
}

export default Proposal
