import { useTranslation } from "react-i18next";
import LoadingSpinner from "../components/UI/loadingSpinner/LoadingSpinner";
import CreateModal from "../components/modal/CreateModal";
import CustomTable from "../components/tables/customTable/CustomTable";
import useDataFetch from "../hook/useDataFetch";
import { IClientsTable } from "../interfaces/Itable";


function Clients() {
    const { t } = useTranslation();
    const PortalUrl=`${process.env.REACT_APP_API_URL}/api/portal/clients`
    const { data, error, loading } = useDataFetch<IClientsTable[]>(PortalUrl);
    let clientTable;
  

    if (loading) {
      clientTable = <LoadingSpinner />;
    }

    if (error) {
      clientTable = (
          <CustomTable headData={[
            "ID",
            "clientName",
            "email", "phone","country","skype","invited","actions"
          ]} bodyData={[]} limit={10} />)
    }
    if (!loading && data) {
      clientTable = (
    <CustomTable headData={[
      "ID",
      "clientName",
      "email", "phone","country","skype","invited","actions"
    ]} bodyData={data} limit={10} />
);
  }
  return (
    <section>
      <div className="portal_creation">
      <h2 className="title">{t("clients")}</h2>
      <CreateModal button={"Create Client"} header={"Create client"} num={10} /> </div>
                {clientTable}
            
    </section>
  );
}

export default Clients;
